<template>
  <div class="app-container" id="unitManagement">
    <div class="filter-container">
      <el-input v-model="listQuery.groupNo" placeholder="车组名称" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" icon="el-icon-circle-plus-outline">添加</el-button>
      <!-- <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel">导出</el-button> -->
    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" fixed prop="id" sortable="custom" align="center" width="80" type="index" :index="indexMethod"></el-table-column>

        <el-table-column label="车组名称" min-width="150px" align="center">
          <template slot-scope="{row}">
            <span v-if="power==1" class="link-type" @click="handleUpdate(row)">{{ row. groupName}}</span>
            <span v-else>{{ row.groupName }}</span>
          </template>
        </el-table-column>

        <el-table-column label="车组长" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.leaderName }}</span>
            <!-- <el-select filterable v-model="scope.row.leaderId" class="filter-item" placeholder="请选择" :class="{removeClass:judgeRemoveIn(teamLeaderOptions,scope.row.leaderId)}">
              <el-option v-for="item in teamLeaderOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
            </el-select> -->
          </template>
        </el-table-column>
        <el-table-column label="司机" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.driverName }}</span>
            <!-- <el-select filterable v-model="scope.row.driverId" class="filter-item" placeholder="请选择" :class="{removeClass:judgeRemoveIn(driverOptions,scope.row.driverId)}">
              <el-option v-for="item in driverOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
            </el-select> -->
          </template>
        </el-table-column>
        <el-table-column label="押运员1" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.safetyName }}</span>
            <!-- <el-select filterable v-model="scope.row.safetyId " class="filter-item" placeholder="请选择" :class="{removeClass:judgeRemoveIn(supercargoOptions,scope.row.safetyId)}">
              <el-option v-for="item in supercargoOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove||item.id==null" />
            </el-select> -->
          </template>
        </el-table-column>

        <el-table-column label="押运员2" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.additionSafetyName }}</span>
            <!-- <el-select filterable v-model="scope.row.additionSafetyId " class="filter-item" placeholder="请选择" :class="{removeClass:judgeRemoveIn(supercargoOptions,scope.row.additionSafetyId)}">
              <el-option v-for="item in supercargoOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
            </el-select> -->
          </template>
        </el-table-column>
        <el-table-column label="业务员1" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.businessName }}</span>

            <!-- <el-select filterable v-model="scope.row.businessId " class="filter-item" placeholder="请选择" :class="{removeClass:judgeRemoveIn(businessManagerOptions,scope.row.businessId)}">
              <el-option v-for="item in businessManagerOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove||item.id==null" />
            </el-select> -->
          </template>
        </el-table-column>
        <el-table-column label="业务员2" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.addBusinessName }}</span>

            <!-- <el-select filterable v-model="scope.row.addBusinessId " class="filter-item" placeholder="请选择" :class="{removeClass:judgeRemoveIn(businessManagerOptions,scope.row.addBusinessId)}">
              <el-option v-for="item in businessManagerOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
            </el-select> -->
          </template>
        </el-table-column>
        <!-- <el-table-column label="绑定道路" min-width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ routeObj[scope.row.linkedRoute] }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="押运员1" min-width="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.additionSafetyName }}</span>
          </template>
        </el-table-column>
        <el-table-column label="业务员1" min-width="200px" align="center">
          <template slot-scope="{row}">
            <span>{{ row.addBusinessName }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="所属大队" width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.teamName }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="建立日期" min-width="150px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.buyDate }}</span>
          </template>
        </el-table-column> -->

        <el-table-column label="操作" fixed="right" align="center" width="400" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <!-- <el-button type="primary" size="mini" @click="handleSave(row)" icon="iconfont iconbaocun">保存</el-button> -->
            <!-- <el-button type="primary" size="mini" @click="handleBind(row)">绑定</el-button> -->
            <!-- <el-button type="warning" size="mini" @click="handleUnbind(row)" :disabled="row.armoredcarId==-1&&row.teamId ==-1&&row.driverId ==-1&&row.securityId ==-1&&row.merchandiserId==-1">解绑</el-button> -->
            <!-- <el-button type="primary" size="mini" @click="handleBindRoute(row)"  icon="iconfont iconsuo">绑定道路</el-button> -->
            <!-- <el-button type="warning" size="mini" @click="handleUnbindRoute(row)" v-else icon="iconfont iconjiesuo5">解绑道路</el-button> -->
            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>

            <el-button type="primary" size="mini" v-if="row.status!==1" @click="handleUse(row)" icon="iconfont iconjihuo">激活</el-button>
            <el-button type="danger" size="mini" v-else @click="deleteConfirmOpen(row)" class="el-icon-delete">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <pagination v-show="total>0" :page-sizes="[5, 10, 15, 20]" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">

        <el-form-item label="车组名称" prop="groupName" v-if="dialogStatus=='create'">
          <el-input v-model="temp.groupName" />
        </el-form-item>

        <el-form-item label="车组长" prop="leaderId">
          <el-select filterable v-model="temp.leaderId" class="filter-item" placeholder="Please select" :class="{removeClass:judgeRemoveIn(teamLeaderOptions,temp.leaderId)}">
            <el-option v-for="item in teamLeaderOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <el-form-item label="司机" prop="driverId">
          <el-select filterable v-model="temp.driverId" class="filter-item" placeholder="Please select" :class="{removeClass:judgeRemoveIn(driverOptions,temp.driverId)}">
            <el-option v-for="item in driverOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <el-form-item label="押运员1" prop="safetyId">
          <el-select filterable v-model="temp.safetyId" class="filter-item" placeholder="Please select" :class="{removeClass:judgeRemoveIn(supercargoOptions,temp.safetyId)}">
            <el-option v-for="item in supercargoOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove||item.id==null" />
          </el-select>
        </el-form-item>

        <el-form-item label="押运员2" prop="additionSafetyId">
          <el-select filterable v-model="temp.additionSafetyId" class="filter-item" placeholder="Please select" :class="{removeClass:judgeRemoveIn(supercargoOptions,temp.additionSafetyId)}">
            <el-option v-for="item in supercargoOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <el-form-item label="业务员1" prop="businessId">
          <el-select filterable v-model="temp.businessId" class="filter-item" placeholder="Please select" :class="{removeClass:judgeRemoveIn(businessManagerOptions,temp.businessId)}">
            <el-option v-for="item in businessManagerOptions" :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove||item.id==null" />
          </el-select>
        </el-form-item>
        <el-form-item label="业务员2" prop="addBusinessId">
          <el-select filterable v-model="temp.addBusinessId" class="filter-item" placeholder="Please select" :class="{removeClass:judgeRemoveIn(businessManagerOptions,temp.addBusinessId)}">
            <el-option v-for="item in businessManagerOptions " :key="item.id" :label="item.name" :value="item.id" :class="{removeClass:item.remove}" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="车组编号" prop="groupNo">
          <el-input v-model.number="temp.groupNo" />
        </el-form-item> -->
        <!-- <el-form-item label="司机" prop="driverId" v-if="dialogStatus=='update'">
          <el-select
            filterable
            v-model="temp.driverId"
            class="filter-item"
            placeholder="Please select"
          >
            <el-option
              v-for="item in driverOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        
        <el-form-item label="所属大队" prop="teamId" v-if="dialogStatus=='update'">
          <el-select
            filterable
            v-model="temp.teamId"
            class="filter-item"
            placeholder="Please select"
          >
            <el-option
              v-for="item in largeTeamOptions"
              :key="item.id"
              :label="item.teamName"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
       
        <el-form-item label="车牌号" prop="armoredcarId" v-if="dialogStatus=='update'">
          <el-select
            filterable
            v-model="temp.armoredcarId"
            class="filter-item"
            placeholder="Please select"
          >
            <el-option
              v-for="item in carOptions"
              :key="item.id"
              :label="item.licenseNo"
              :value="item.id"
            />
          </el-select>
        </el-form-item> -->
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():handleSave1(temp)">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" :title="bindMap[bindDialogStatus]" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" :model="bindTemp" label-position="left">

        <el-form-item :label="`${bindMap[bindDialogStatus]}类型`" prop="type">
          <el-select filterable v-model="bindTemp.type" class="filter-item" placeholder="Please select">
            <el-option v-for="item in bindTypeOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.disabled" />
          </el-select>
        </el-form-item>
        <el-form-item label="司机" prop="driverId" v-if="bindTemp.type==3&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.driverId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in driverOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="所属大队" prop="teamId" v-if="bindTemp.type==2&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.teamId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="押运员A" prop="securityId" v-if="bindTemp.type==4&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.securityId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in supercargoOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <el-form-item label="押运员B" prop="merchandiserId" v-if="bindTemp.type==5&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.merchandiserId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in businessManagerOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.remove" />
          </el-select>
        </el-form-item>
        <el-form-item label="车牌号" prop="armoredcarId" v-if="bindTemp.type==1&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.armoredcarId" class="filter-item" placeholder="Please select">
            <el-option v-for="item in carOptions" :key="item.id" :label="item.licenseNo" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindDialogStatus=='bind'?bindSubmit():unbindSubmit()">确定</el-button>
      </div>
    </el-dialog>
    <!-- <el-dialog id="routeDialog" title="绑定道路" :visible.sync="routeDialogFormVisible" center>
      <el-form ref="routeForm" :rules="routeRules" :model="routeTemp" label-position="left" class="formStyle">
        <el-form-item label="路线" prop="routeIds">
          <el-select filterable v-model="routeTemp.routeIds " multiple class="filter-item" placeholder="Please select">
            <el-option v-for="item in routeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="routeSubmit()">确定</el-button>
      </div>
    </el-dialog> -->
  </div>
</template>

<script>
import Pagination from "@/components/Pagination"; // secondary package based on el-pagination
//车组列表，添加车组，编辑，删除，搜索
import {
  carGrouplist,
  carGroupadd,
  carGroupupdate,
  carGroupdelete,
  carGroupSearch,
  bind,
  unbind,
  disable,
  enable,
  bindRoute,
  unbindRoute,
  queryBindedRoutes
} from "../../../api/managementArchives/unitManagement";
import {
  teamsSelect,
  personsSelect,
  carsSelect,
  exportExcelReq,
  mapSelect
} from "../../../api/managementArchives/common"; //大队下拉框数据，人员下拉框数据，车辆下拉框数据
import { parseToNumber } from "../../../../src/common"; //转换位数字

let myLoading = undefined; // 弹窗服务调用
//未选择
const unSelect = (rule, value, callback) => {
  if (value == null) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
export default {
  name: "unitManagement",
  components: { Pagination },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //车组列表
      total: 0, //车组数量
      indexMethod: 1, // 序号
      listLoading: true,
      listQuery: {
        page: 1,
        limit: 10,
      },

      largeTeamOptions: [{ id: null, teamName: "请选择" }], //大队下拉数据
      driverOptions: [{ id: null, name: "请选择" }], //司机下拉数据
      bindTypeOptions: [
        { id: null, name: "请选择" },
        { id: 1, name: "车辆" },
        { id: 2, name: "大队" },
        { id: 3, name: "司机" },
        { id: 4, name: "押运员A" },
        { id: 5, name: "押运员B" },

      ], //绑定类型下拉数据
      teamLeaderOptions: [{ id: null, name: "请选择" }], //车组长下拉数据
      supercargoOptions: [{ id: null, name: "请选择" }], //押运员下拉数据
      businessManagerOptions: [{ id: null, name: "请选择" }], //业务员下拉数据
      additionSafetyOptions: [{ id: null, name: "请选择" }], //押运员下拉数据
      addBusinessOptions: [{ id: null, name: "请选择" }], //业务员下拉数据
      carOptions: [{ id: null, licenseNo: "请选择" }], //车辆下拉数据
      // //添加或编辑使用的数据
      temp: {
        // id: undefined,
        // teamId: undefined,
        // teamName: undefined,
        groupName: "",
        // groupNo: undefined,
        // driverId: undefined,
        // driverName: undefined,
        // leaderId: undefined,
        // leaderName: undefined,
        // securityId: undefined,
        // securityName: undefined,
        // merchandiserId: undefined,
        // merchandiserName: undefined,
        // armoredcarId: undefined,
        // armoredcarLicenseNo: undefined,
      },
      bindTemp: {
        relatedId: -1,
        type: 1,
        groupId: -1
      },
      BindDialogFormVisible: false,
      dialogFormVisible: false, //添加或编辑窗口显隐
      dialogStatus: "", //控制是编辑窗口还是添加窗口
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      textMap: {
        update: "编辑",
        create: "添加"
      },
      bindobj: {},

      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      getSelectPromises: [],

      //添加或编辑表单校验
      rules: {
        // groupNo: [
        //   {
        //     required: true,
        //     type: "number",
        //     message: "请填写车组编号,且必须是数字",
        //     trigger: "blur"
        //   }
        // ],
        driverId: [
          { required: true, message: "请选择司机", trigger: "change" }
        ],
        groupName: [
          { required: true, message: "请填写车组名称", trigger: "blur" }
        ],
        leaderId: [
          { required: true, message: "请选择车组长", trigger: "change" }
        ],
        safetyId: [
          { required: true, message: "请选择押运员", trigger: "change" }
        ],
        businessId: [
          { required: true, message: "请选择业务员", trigger: "change" }
        ]
      },
      bindRules: {
        type: [
          { required: true, message: "请选择绑定类型", trigger: "change", validator: unSelect }
        ],
        driverId: [
          { required: true, message: "请选择绑定司机", trigger: "change", validator: unSelect }
        ],
        teamId: [
          { required: true, message: "请选择绑定大队", trigger: "change", validator: unSelect }
        ],
        securityId: [
          { required: true, message: "请选择绑定押运员A", trigger: "change", validator: unSelect }
        ],
        merchandiserId: [
          { required: true, message: "请选择绑定押运员B", trigger: "change", validator: unSelect }
        ],
        armoredcarId: [
          { required: true, message: "请选择绑定车辆", trigger: "change", validator: unSelect }
        ]
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      routeDialogFormVisible: false,
      routeRules: {
        routeIds: [
          { required: true, message: "请选择路线", trigger: "change", validator: unSelect }
        ],

      },
      routeTemp: {routeIds:[]},
      routeOptions: [{ id: null, routeId: "请选择" }],
      routeObj:{},
    };
  },
  created() {
    this.getList(); //初始化获取车组列表
    this.getSelect(); //初始化弹窗的下拉框
  },
  methods: {
     deleteConfirmOpen(row) {
      this.$confirm('确定进行此操作吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.handleDelete(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    queryBindedRoutesById(id){
     queryBindedRoutes({ groupId:id  })
        .then(res => {
          if (res.status == 0) {
            if(! res.data){
            res.data=[];
            }
            this.routeTemp.routeIds=res.data.map((item,index)=>{
              return item.id
            });
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
        });
    },
    handleBindRoute(row) {
      this.routeDialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["routeForm"].clearValidate();
      });
      this.routeTemp = { id: row.id,routeIds:[] };
      this.queryBindedRoutesById(row.id);
    },
    handleUnbindRoute(row) {
      myLoading = this.$loading({ target: "#routeDialog .el-dialog" });
      unbindRoute({ id: row.id })
        .then(res => {
          if (res.status == 0) {
            this.getList();
            this.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          myLoading.close();
        });
    },
    routeSubmit() {
      this.$refs["routeForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#routeDialog .el-dialog" });
          let subData = { ...this.routeTemp }
          bindRoute(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.routeDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      });
    },
    //判定select选中的是否在数组里
    judgeRemoveIn(arr, id) {
      let flag = false;
      let res = arr.some(item => {
        if (item.id === id && item.remove === true) {
          flag = true;
        }
      })
      return flag
    },
    handleSelectOptions(option, id, name, idN, nameN) {
      let hasItem = false;
      for (let i = 0; i < option.length; i++) {
        if (option[i][idN] == id) {
          hasItem = true;
          return;
        }
      }
      if (hasItem === false) {
        if (id !== null) {
          option.unshift({ [idN]: id, [nameN]: name, remove: true });
        }
      }
    },
    //获取车组列表
    getList() {
      let _t = this;
      _t.listLoading = true;
      carGrouplist({
        page: this.listQuery.page,
        limit: this.listQuery.limit,
        status: [1]
      })
        .then(res => {
          this.listLoading = false;
          if (res.status == 0) {
            this.list = res.data;
            this.total = res.count;
            // this.dealData();
            this.getSelect();
            for (let i = 0; i < this.list.length; i++) {
              let item = this.list[i]
              Promise.all(this.getSelectPromises).then(() => {
                _t.handleSelectOptions(_t.teamLeaderOptions, item.leaderId, item.leaderName, 'id', 'name');
                _t.handleSelectOptions(_t.supercargoOptions, item.safetyId, item.safetyName, 'id', 'name');
                _t.handleSelectOptions(_t.businessManagerOptions, item.businessId, item.businessName, 'id', 'name');
                _t.handleSelectOptions(_t.supercargoOptions, item.additionSafetyId, item.additionSafetyName, 'id', 'name');
                _t.handleSelectOptions(_t.businessManagerOptions, item.addBusinessId, item.addBusinessName, 'id', 'name');
                _t.handleSelectOptions(_t.driverOptions, item.driverId, item.driverName, 'id', 'name');


              })
            }

            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },
    dealData() {
      this.list.forEach((item, index) => {
        if (this.list[index].teamId == -1) {
          this.list[index].teamId = null;
          this.list[index].teamName = null;
        }
        if (this.list[index].driverId == -1) {
          this.list[index].driverId = null;
          this.list[index].driverName = null;
        }
        if (this.list[index].leaderId == -1) {
          this.list[index].leaderId = null;
          this.list[index].leaderName = null;
        }
        if (this.list[index].securityId == -1) {
          this.list[index].securityId = null;
          this.list[index].securityName = null;
        }
        if (this.list[index].merchandiserId == -1) {
          this.list[index].merchandiserId = null;
          this.list[index].merchandiserName = null;
        }
        if (this.list[index].armoredcarId == -1) {
          this.list[index].armoredcarId = null;
          this.list[index].armoredcarLicenseNo = null;
        }
      });
    },

    //重置
    resetTemp() {
      this.temp = {
        // id: undefined,
        // teamId: undefined,
        // teamName: undefined,
        groupName: "",
        // driverId: undefined,
        // driverName: undefined,
        // leaderId: undefined,
        // leaderName: undefined,
        // securityId: undefined,
        // securityName: undefined,
        // merchandiserId: undefined,
        // merchandiserName: undefined,
        // armoredcarId: undefined,
        // armoredcarLicenseNo: undefined
      };
    },
    // selectChange(val) {
    //   if (val == 1) {
    //     this.bindTemp.armoredcarId = this.bindobj.armoredcarId;
    //   } else if (val == 2) {
    //     this.bindTemp.teamId = this.bindobj.teamId;
    //   } else if (val == 3) {
    //     this.bindTemp.driverId = this.bindobj.driverId;
    //   } else if (val == 4) {
    //     this.bindTemp.securityId = this.bindobj.securityId;
    //   } else if (val == 5) {
    //     this.bindTemp.merchandiserId = this.bindobj.merchandiserId;
    //   }
    // },
    //点击绑定
    handleBind(row) {
      // this.bindobj = { ...row };
      this.$nextTick(() => {
        this.$refs["bindForm"].clearValidate();
      });

      this.bindTypeOptions = [
        { id: -1, name: "请选择" },
        { id: 1, name: "车辆" },
        { id: 2, name: "大队" },
        { id: 3, name: "司机" },
        { id: 4, name: "押运员A" },
        { id: 5, name: "押运员B" }

      ];
      this.bindDialogStatus = "bind";
      this.bindTemp = {
        groupId: row.id,
        type: 1,
        armoredcarId: row.armoredcarId,
        teamId: row.teamId,
        driverId: row.driverId,
        securityId: row.securityId,
        merchandiserId: row.merchandiserId,

      }
      this.BindDialogFormVisible = true;
    },
    //点击解绑
    handleUnbind(row) {
      if (row.armoredcarId == -1) { this.bindTypeOptions[1].disabled = true }
      if (row.teamId == -1) { this.bindTypeOptions[2].disabled = true }
      if (row.driverId == -1) { this.bindTypeOptions[3].disabled = true }
      if (row.securityId == -1) { this.bindTypeOptions[4].disabled = true }
      if (row.merchandiserId == -1) { this.bindTypeOptions[5].disabled = true }

      this.bindDialogStatus = "unbind";
      this.bindTemp = {
        groupId: row.id,
        type: -1
      }
      this.BindDialogFormVisible = true;
    },
    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          if (this.bindTemp.type == 1) {
            this.bindTemp.relatedId = this.bindTemp.armoredcarId;
          } else if (this.bindTemp.type == 2) {
            this.bindTemp.relatedId = this.bindTemp.teamId;
          } else if (this.bindTemp.type == 3) {
            this.bindTemp.relatedId = this.bindTemp.driverId;
          } else if (this.bindTemp.type == 4) {
            this.bindTemp.relatedId = this.bindTemp.securityId;
          } else if (this.bindTemp.type == 5) {
            this.bindTemp.relatedId = this.bindTemp.merchandiserId;
          }
          let subData = {
            type: this.bindTemp.type,
            relatedId: this.bindTemp.relatedId,
            groupId: this.bindTemp.groupId
          }
          bind(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.BindDialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      })
    },
    unbindSubmit() {
      myLoading = this.$loading({ target: "#myDialog .el-dialog" });
      unbind(this.bindTemp)
        .then(res => {
          if (res.status == 0) {
            this.getList();
            this.$message.success(res.msg);
            this.BindDialogFormVisible = false;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          myLoading.close();
        });
    },
    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //添加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData = { ...this.temp }
          if (subData.additionSafetyId) {
            subData.addSafetyId = subData.additionSafetyId;
            delete subData.additionSafetyId;
          }
          // if (!subData.groupNo && subData.groupNo !== 0) {
          //   subData.groupNo = -1;
          // }

          // this.temp = this.changToNull(this.temp);
          carGroupadd(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("添加成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = { ...row };

      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });

    },
    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData = { ...this.temp }
          carGroupupdate(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success("编辑成功");
                this.dialogFormVisible = false;
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        }
      });
    },
    //保存1
    handleSave1(row) {
      let _t = this;
      _t.listLoading = true;
      let safetyId = -1, businessId = -1;
      // if (row.safetyId !== null) {
      //   safetyId = row.safetyId
      // }
      // if (row.businessId !== null) {
      //   businessId = row.businessId
      // }
      let subData = {
        groupId: row.id,
        leaderId: row.leaderId,
        securityId: row.safetyId,
        merchandiserId: row.businessId,
        // addSecurityId: row.additionSafetyId,
        // addBusinessId: row.addBusinessId,
        driverId: row.driverId

      }
      if (row.additionSafetyId !== null) {
        subData.addSecurityId = row.additionSafetyId
      }
      if (row.addBusinessId !== null) {
        subData.addBusinessId = row.addBusinessId
      }

      bind(subData)
        .then(res => {
          if (res.status == 0) {
            this.dialogFormVisible = false;

            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },

    //保存
    handleSave(row) {
      let _t = this;
      _t.listLoading = true;
      let safetyId = -1, businessId = -1;
      // if (row.safetyId !== null) {
      //   safetyId = row.safetyId
      // }
      // if (row.businessId !== null) {
      //   businessId = row.businessId
      // }
      let subData = {
        groupId: row.id,
        leaderId: row.leaderId,
        securityId: row.safetyId,
        merchandiserId: row.businessId,
        // addSecurityId: row.additionSafetyId,
        // addBusinessId: row.addBusinessId,
        driverId: row.driverId

      }
      if (row.additionSafetyId !== null) {
        subData.addSecurityId = row.additionSafetyId
      }
      if (row.addBusinessId !== null) {
        subData.addBusinessId = row.addBusinessId
      }

      bind(subData)
        .then(res => {
          if (res.status == 0) {
            this.dialogFormVisible = false;

            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }

            this.getList();

            _t.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },
    //激活
    handleUse(row) {
      let _t = this;
      _t.listLoading = true;
      enable({
        groupId: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },
    //禁用
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      disable({
        groupId: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success(res.msg);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },
    //搜索
    handleFilter() {
      if (this.listQuery.groupNo == "" || this.listQuery.groupNo == undefined) {
        this.listQuery.groupNo = null;
        this.getList();
        return;
      }
      carGroupSearch({
        keyword: this.listQuery.groupNo
      }).then(res => {
        if (res.status == 0) {
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = [];
          this.list = res.data;

          this.total = 0;
          // this.dealData();
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //筛选出select选择的值和内容，参数分别代表（select的v-model值，option数组，option值，option内容，后台需要的id,后台需要的name）
    selectFilter(vId, selectList, id, name, id1, name1) {
      let obj = {};
      obj = selectList.find(item => {
        //这里的selectList就是上面遍历的数据源
        return item[id] === vId; //筛选出匹配数据
      });
      this.temp[name1] = obj[name];
    },
    //初始化下拉框
    getSelect() {
      let that = this;
      //路线
      mapSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.routeOptions = res.data;
          that.routeObj={};
          that.routeOptions.forEach((item,index)=>{
          that.routeObj[item.id]=item.name
          })
        } else {
          if (res.data == null) {
            this.routeOptions = [];
          }
          this.$message.error(res.msg);
        }


      });
      teamsSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.largeTeamOptions = res.data;
          that.largeTeamOptions.unshift({ id: null, teamName: "请选择" });
        } else {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
      carsSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.carOptions = res.data;
          that.carOptions.unshift({ id: null, licenseNo: "请选择" });
        } else {
          if (res.data == null) {
            this.carOptions = [];
            this.carOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
      let p4 = new Promise((resolve, reject) => {
        personsSelect({ work: 1, status: 1 }).then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              return;
            }
            that.driverOptions = res.data;
            // that.driverOptions.unshift({ id: null, name: "请选择" });
          } else {
            if (res.data == null) {
              this.driverOptions = [];
              this.driverOptions.pop();
            }
            this.$message.error(res.msg);
          }
          resolve();
        });
      });

      let p1 = new Promise((resolve, reject) => {
        personsSelect({ work: 2, status: 1 }).then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              return;
            }
            that.teamLeaderOptions = res.data;
            // that.teamLeaderOptions.unshift({ id: null, name: "请选择" });
          } else {
            if (res.data == null) {
              this.teamLeaderOptions = [];
              this.teamLeaderOptions.pop();
            }
            this.$message.error(res.msg);
          }
          resolve();
        });
      })
      let p2 = new Promise((resolve, reject) => {
        personsSelect({ work: 3, status: 1 }).then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              return;
            }
            that.supercargoOptions = res.data;
            that.supercargoOptions.unshift({ id: null, name: "请选择" });
          } else {
            if (res.data == null) {
              this.supercargoOptions = [];
              this.supercargoOptions.pop();
            }
            this.$message.error(res.msg);
          }
          resolve();

        });
      })
      let p3 = new Promise((resolve, reject) => {
        personsSelect({ work: 4, status: 1 }).then(res => {
          if (res.status == 0) {
            if (res.data == null) {
              return;
            }
            that.businessManagerOptions = res.data;
            that.businessManagerOptions.unshift({ id: null, name: "请选择" });
          } else {
            if (res.data == null) {
              this.businessManagerOptions = [];
              this.businessManagerOptions.pop();
            }
            this.$message.error(res.msg);
          }
          resolve();

        });
      })
      this.getSelectPromises = [p1, p2, p3, p4];


    },
    //由于提交的时候如果对应的id为null,对应的name也必须要为null,在这里就统一处理
    changToNull(data) {
      if (data.teamId == null) {
        data.teamName = null;
      }
      if (data.driverId == null) {
        data.driverName = null;
      }
      if (data.leaderId == null) {
        data.leaderName = null;
      }
      if (data.securityId == null) {
        data.securityName = null;
      }
      if (data.merchandiserId == null) {
        data.merchandiserName = null;
      }
      if (data.armoredcarId == null) {
        data.armoredcarLicenseNo = null;
      }
      return data;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      // 查找数据
      exportExcelReq({ excelType: 6, title: '车组信息' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "车组信息" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      });
    }
  }
};
</script>
