    .el-select {
      .el-input__inner {
        height: 60px;
        line-height: 60px;
        background-color: transparent;
        border: 0;
        //   text-align: center;
      }
      .el-input__icon {
        height: 60px;
        line-height: 60px;
      }
    }
  }
}
</style>







